class News extends React.Component {
  constructor(props){
    super(props);
    this.state = { 
      info: '',
      elem: [],
      Send: ''
    };
  }
  mySubmitHandler = (event) => {
    event.preventDefault();
    let nam = event.target.name;
    let val = event.target.value;
    let el = event.target.elem;
    if(this.state.info){
    el = <p style={{color:"#1aff1a"}}>{ this.state.info} </p>
    localStorage.setItem('1', JSON.stringify(el));
    }else if(this.state.Send){
      el = <p></p>
      localStorage.clear();
    }
    this.setState({elem: el});
    this.setState({[nam]: val});
  }

  myChangeHandler = (event) => {
    let nam = event.target.name;
    let val = event.target.value;
    this.setState({[nam]: val});
  }

  render() {
    return (
      <form onSubmit={ this.mySubmitHandler}>

        <br></br>
        {this.state.elem}
        <p>Enter news:</p>
        <input
          style={{backgroundColor: "lightblue"}}
          type="text"
          name='info'
          onChange={this.myChangeHandler}
        />
        <br></br>
        <input
      type='submit'
      value = "Clear"
      />
      <input 
      type='submit'
      value = "Send"
      />
      </form>
    );
  }
}

ReactDOM.render(<News />, document.getElementById('news'));


